@import '../../style/variables';

.header {
  background-image: url(../../assets/images/hello.jpg);
}
.contactSuccess {
  text-align: center;
  font-size: 26px;
}

.error {
  padding: 5px 0;
  font-size: 16px;
  color: var(--cs-darkred);
  overflow: hidden;
}

.error1 { animation: grow 0.07s ease-out; }
.error2 { animation: grow 0.14s ease-out; }
.error3 { animation: grow 0.21s ease-out; }
.error4 { animation: grow 0.28s ease-out; }
.error5 { animation: grow 0.35s ease-out; }

@keyframes grow {
  0%   { height: 0px; opacity: 0; }
  70%  { height: 35px; }
  100% { height: 30px; opacity: 1; }
}

form {
  width: 80%;
  margin: auto;
}

label {
  display: block;
  margin-bottom: 5px;
  font-family: 'Open Sans', serif;
  font-weight: 700;
  font-size: 18px;
}

input, textarea {
  font-size: 24px;
  border-radius: 2px;
  padding: 4px;
  width: 100%;
  border: 1px solid var(--cs-gray);
}

:global .formgroup {
  margin-bottom: 10px;
}

button {
  border: 1px solid var(--cs-gray);
  border-radius: 2px;
  background-color: var(--cs-red);
  color: white;
  width: 100%;
  font-size: 20px;
  margin-top: 5px;
  padding: 10px;
}


