@import '../../style/variables.css';

.header {
	display: flex;
	flex-direction: column;
	padding-top: 10px;
}

.logo {
	max-height: 40px;
	filter: drop-shadow( 2px 2px 2px rgba(0,0,0,0.5) );
}

.subheader {
	display: flex;
	flex: 2 1 100%;
	justify-content: space-between;
	flex-direction: column;
}

.branding {
	margin: 0;
	padding: 0 5px;
	letter-spacing: 2px;
	text-align: center;
	font-size: 18px;
	filter: drop-shadow( 2px 2px 2px rgba(8,8,8,0.25) );
}

.branding b {
	color: black;
}

.cursor {
	color: black;
	transition: opacity 0.15s;
}

.nav {
	display: flex;
	justify-content: space-around;
	margin-top: 5px;
}

@media only screen and (max-width : 1000px) and (min-width: 800px) {
	.header {
		margin: 0 15px;
	}
}

@media only screen and (min-width : 800px) {
	.header {
		flex-direction: row;
	}
	.logo {
		max-height: 60px;
	}
	.branding {
		text-align: left;
		font-size: 36px;
		margin-top: -10px;
	}
	.nav {
		margin-left: 40%;
		justify-content: space-between;
	}
}

.nav a {
	color: var(--cs-gray);
	text-decoration: none;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 18px;
	border-bottom: 2px solid transparent;
}

.nav a:hover {
	border-bottom: 2px solid var(--cs-darkred);
}

.nav a.active {
	text-decoration: none;
	color: var(--cs-darkred);
}


