@import '../../style/variables.css';

.home {
}

.slides {
  margin: 10px 0;
}

.blurb {
  composes: row from '../../style/flexboxgrid.css';
  margin: 20px 0;
}

.blurbtext {
  composes: col-md-8 from '../../style/flexboxgrid.css';
  padding: 10px;
}

.blurbimage {
  composes: col-md-4 from '../../style/flexboxgrid.css';
  display: flex;
  justify-content: center;
  flex: 1 0 auto;
  align-items: flex-start;
}

.blurbimage img {
  border-radius: 4px;
}

:global .slider-slide > div {
  height: 250px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  color: white;
  text-shadow: 2px 2px 5px black, 0 0 2px black;
  border-radius: 4px;
}

:global .slider-control-bottomcenter button {
  color: white !important;
  text-shadow: 2px 2px 5px black, 0 0 2px black;
}

:global .slider-control-centerleft,
:global .slider-control-centerright
{
  opacity: 0.25;
  transition: all 0.25s ease;
}

:global .slider-control-centerleft:hover,
:global .slider-control-centerright:hover
{
  opacity: 1;
}

.augment {
  background-image: url(../../assets/images/augment.jpg);
}

.endToEnd {
  background-image: url(../../assets/images/end-to-end.jpg); 
}

.projectManagement {
  background-image: url(../../assets/images/project-management.jpg); 
}

.consulting {
  background-image: url(../../assets/images/consulting.jpg); 
}


