@import './flexboxgrid.css';
@import '//fonts.googleapis.com/css?family=Open+Sans:400,700|Open+Sans+Condensed:400,700italic';
@import './variables.css';

html, body {
	/* height: 100%; */
	width: 100%;
	padding: 0;
	margin: 0;
	background: #FAFAFA;
	font-family: 'Open Sans Condensed', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

body {
	background: linear-gradient(to bottom, rgba(200,200,200,1) 0%,rgba(210,210,210,1) 100px,rgba(255,255,255,1) 100%);
}

#app {
	position: relative;
	min-height: 100vh;
	max-width: 960px;
	margin: auto;
}

h1 {
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	line-height: 51px;
	font-size: 46px;
	text-transform: uppercase;
	margin: 0 0 23px 0;		
}

h2 {
	font-family: 'Open Sans', serif;
	font-weight: 700;
	font-size: 26px;
	line-height: 32px;
	margin: 0 0 18px 0;
}

p {
	font-family: 'Open Sans Condensed', serif;
	font-size: 20px;
	line-height: 26px;	
	margin: 0 10px 26px 10px;
	color: var(--cs-gray);
}

a {
	color: var(--cs-darkred);
}

a:hover, a:active {
	color: var(--cs-red);
}

.page-header {
  height: 250px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  color: white;
  text-shadow: 5px 5px 10px black, 0 0 5px black;
	border-radius: 4px;
	margin: 10px 0 20px 0;
}

.jumbo {
  margin: 60px;
  text-align: center;
  font-size: 30px;
  color: var(--cs-darkred);
  font-family: 'Open Sans', sans-serif;
}

.jumbo cite {
	display: block;
	font-size: 18px;
	color: var(--cs-gray);
	margin-top: 5px;
	text-align: right;
}

.jumbo cite::before { 
	content: "\2015";
}


