@import '../../style/variables';

.footer {
  padding: 50px 10px 10px 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.footer > nav {
  font-size: 14px;
}

.contact {
  border: 1px solid var(--cs-darkred);
  line-height: 36px;
  padding: 5px;
  border-radius: 4px;
  text-decoration: none;
  color: white;
  background-color: var(--cs-red);
}

.contact:hover {
  color: white;
  background-color: var(--cs-darkred);
}


