@import '../../style/variables';

.capabilities {
}

.header {
  background: url(../../assets/images/puzzle.jpg);
}

.skillmenu {
  margin: 30px 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.skillicon {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 5px 10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: var(--cs-gray);
  color: white;
  font-size: 28px;
  transition: font-size 0.25s ease;
}

.skillicon div {
  position: absolute;
  width: 80px;
  bottom: -25px;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  color: var(--cs-gray);
}

.skillicon:hover {
  color: white;
  background-color: var(--cs-red);
  font-size: 34px;
}

.skillicon:hover div {
  color: var(--cs-red);
}

.skilldetail {
  composes: row from '../../style/flexboxgrid';
  margin: 80px 30px;
}

.skilldetailicon {
  composes: col-md-2 from '../../style/flexboxgrid';
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80px;
  color: var(--cs-gray);
}

@media only screen and (max-width: 600px) {
  .skilldetailicon {
    align-items: flex-start;
    font-size: 30px;
    padding: 0 10px;
  }
}

.active, .skilldetailicon:hover {
  color: var(--cs-red);
  filter: drop-shadow( 2px 2px 2px rgba(0,0,0,0.25) );
}

.skilldetailtext {
  composes: col-md-10 from '../../style/flexboxgrid';
}


